$backgroundColor: #efefef;
$radius: 40%;

form {
  display: inline-block;
  margin: auto;
  width: 200px;

  label {
    display: flex;
    margin: 1rem 0;

    &.methods {
      button {
        cursor: pointer;
        margin-right: 1rem;
        padding: .25rem .5rem;
        background: #808080;

      &:active,&:hover, &:focus {
          background: red;
        }
          }
    }
  }
}
